import React from 'react';
import { Table, Divider, Tag ,DatePicker, Button } from 'antd';
import { Row, Col ,message} from 'antd';
import moment from 'moment';
import { Modal } from 'antd';
import {Select,Input} from 'antd';
import { getFactTableData ,approveTimeSheets} from './Actions';
import {connect} from 'react-redux';
import Lightbox from 'react-images';
import Logo from './../../Images/logo.png';
import {activegetClients} from './Actions';
import { Document , Page } from 'react-pdf';
import axios from 'axios';
import	pdf from './output.pdf';

const { TextArea } = Input;

const format = 'HH:mm:ss';
const Option = Select.Option;
const InputStyle={
	width:"100%",
	padding:"5px 5px"
}
const LabelStyle = {

	textAlign:"center",
	paddingTop:"10px"
}
const submitStyle = {
	width:"90%"
}

const error =(data)=>{
	message.error(data);
}


const success =(data)=>{
	message.success(data);
}

const { MonthPicker, RangePicker } = DatePicker;
const { Column, ColumnGroup } = Table;

const mapDispatchToProps = (dispatch)=>{
	return{
		
		activegetClients : ( )=>{
			dispatch(activegetClients());
		},
		
	}
}

const mapStateToProps = (state)=>{
	return {
		userid : state.Reducer.user.UserId,
		factTableData : state.StaffDashboard.factTableData,
		role : state.Reducer.user.Role,
		activeClients:state.ClientDetails.activeClients,
		
	}
}

class ProcessTimeSheets extends React.Component{

	constructor(props){
		super(props);
		this.state = { 
			visible: 'true',
			startDate: null,
			endDate:null,
			client : '',
			ack_path:null,
			lightboxIsOpen:false,
			factTableData:[],
			numPages: null,
    		pageNumber: 1,
    		url:'/acks/251question.jpg',
    		pdfStatus:true,
			
		 }

	}
  handleChangeClient = (value)=> {
	  this.setState({
	  	client : value,
	  })
	 
	}

  componentDidMount(){

  	let startDate =  new Date();
    let endDate = new Date();
	startDate.setDate(startDate.getDate() - 6);

	this.setState({
		startDate:startDate.toISOString().slice(0,10),
		endDate:endDate.toISOString().slice(0,10),
	})

  		
	this.props.activegetClients();
	
  }

  onDocumentLoadSuccess = ({ numPages }) => {
    this.setState({ numPages });
  }

  loadJobs = ()=>{

  	if(this.state.client === ""){

  		error("Select Client")
  	}else{
  			let data = {
			startDate : this.state.startDate,
			endDate : this.state.endDate,
			client : this.state.client,
		}

		axios.post('/api/getProcessFactTableDetails/', data).then( function(res){

			console.log(res.data)
			this.setState({
				factTableData : res.data.factTable,
				//url:'/acks/pdf.pdf',

			})

		}.bind(this)).catch((err)=>{
			error("Issue with Database");
		})

  	}
 		
	
  }

  OnChangeDate = (value,dateString)=>{
		console.log(dateString);
		this.setState({
			startDate : dateString[0],
			endDate : dateString[1],
	
		})

	}

	processInvoice = ()=>{

		this.setState({
			pdfStatus: true
		})

		if(this.state.client === "" && this.state.factTableData.length === 0){

  			error("Select Client")
  		}else{
  			let data = {
				startDate : this.state.startDate,
				endDate : this.state.endDate,
				client : this.state.client,
			}

		axios.post('/api/processInvoice/', data).then( function(res){

			console.log(res.data)
			this.setState({
				
				url: 'http://35.154.35.118/Payslips/' + res.data.url + '.pdf',
				pdfStatus : false,
			})

			}.bind(this)).catch((err)=>{
				error("Issue with Database");
				
			})

  		}

	}

	loadClients = (clients)=>{
     const listItems = clients.map((number) =>
						  <Option key = {number} value={number}>{number}</Option>
						);
		return listItems;
	}


render(){
			const { pageNumber, numPages } = this.state;
			let startDate =  new Date();
			let endDate = new Date();
			startDate.setDate(startDate.getDate() - 6);
			const dateFormat = 'YYYY/MM/DD';

	const columns = [{
				  title: 'Job ID',
				  dataIndex: 'jobid',
				 width:150,
				},{
				  title: 'Hospital',
				  dataIndex: 'client',
				  width:150,
				  
				},{
				  title: 'Name',
				  dataIndex: 'Fname',
				  width:150,
				  
				},{
				  title: 'Staff',
				  dataIndex: 'stafftype',
				 	width:150,
				  
				},{title: 'Date',
				  dataIndex: 'date',
				 width:250,
				
				},{
				  title: 'Start Time',
				  dataIndex: 'start_time',
				  width:250,
		  
				}, {
				  title: 'End Time',
				  dataIndex: 'end_time',
				  width:250,
				  
				},
				{
				  title: 'Remarks',
				  dataIndex: 'remarks',
				  width:250,			  
				},
				{
				  title: 'Action',
				  dataIndex: 'Action',
				  key: 'Action',
				  fixed:"right",
				  width:100,
				  render: (text, record) => (
				    <span>
    
				          {record.flag == 'Y' ? 'Approved' : 'Not Approved'}
				       
				    </span>

				  ),
				  
				  
				}];

	return(
		<div>
			<Row>
				<Col xs={24} sm={24} md={24} lg={24} xl={24}>

					
					
					
				 
				</Col>
			</Row>
			<Row>
				<Col>
				</Col>
				<Col>
					<Row>
						 <Col xs={0} sm={0} md={2} lg={2} xl={2}> 

      					 </Col>
						<Col xs={24} sm={24} md={20} lg={20} xl={20}> 
							 <Row>
							  	<Col xs={4} sm={4} md={4} lg={4} xl={4}>
							  		<b>	Client </b>
							  	</Col>	
							  	<Col xs={12} sm={12} md={6} lg={6} xl={6}>
							  	  	<Select value={this.state.client} onChange={this.handleChangeClient} style = {{width:"100%"}}>
										{this.loadClients(this.props.activeClients)}
								    </Select>
							  	</Col>	

							  </Row>
							<Row>
								<Col xs={4} sm={4} md={4} lg={4} xl={4}>
							  		<b>	Select Date Range </b>
							  	</Col>	
								
								<Col xs={12} sm={12} md={6} lg={6} xl={6}>
									<RangePicker defaultValue={[moment( startDate, dateFormat), moment(endDate, dateFormat)]} onChange = {this.OnChangeDate}/>

								</Col>

								<Col xs={12} sm={12} md={6} lg={6} xl={6} >
				        			<Button type="primary" onClick={this.loadJobs}> Load </Button>
				        		
								</Col>

							</Row>
							
												
			     			<Row>
			     				<Col xs={24} sm={24} md={24} lg={24} xl={24} >
           						 	<Table columns={columns} 
           						 			size="medium" 
           						 			scroll={{ x: 1800 }}
           						 			dataSource={this.state.factTableData} />
					   			</Col>
          					</Row>

          					<Row>

								<Col xs={12} sm={12} md={6} lg={6} xl={6} >
				        			
				        			<Button type="primary" onClick={this.processInvoice} > Process Invoice </Button>

				        			<a  href={this.state.url} target = "_blank" disabled = {this.state.pdfStatus}> Download </a>

								</Col>

							</Row>
						</Col>
       					<Col xs={0} sm={0} md={2} lg={2} xl={2}> 
						</Col>
					</Row>	
					</Col>
					<Col>
					</Col>
				</Row>
			</div>

		)


	}

}

export default connect ( mapStateToProps ,mapDispatchToProps ) (ProcessTimeSheets);


