import React from 'react';
import Logo from './../Images/logo.png';
import {Row, Col,Button,Icon} from 'antd';
import { LogOutButton } from './Elements/Ccbutton';
import { Avatar, Badge } from 'antd';
import { connect } from 'react-redux';
import {logout} from '../Redux/Actions';
import {Redirect} from 'react-router-dom'
import './Header.css';

const baseStyle = {
	width:'100%',
	background: '#f1f1f1',
	color:"black",
	padding:"3px",
	
}




const divStyle = {

  textOverflow : "hidden",
  overflow : "hidden"

}

const mapStateToProps = (state) => {
	
    return {
        Fname: state.Reducer.user.Fname,
        Lname: state.Reducer.user.Lname,
        Role:state.Reducer.user.Role,
        authenticated:state.Reducer.isauthenticated

   }
}

const mapDispatchToProps = (dispatch)=>{
	return{
		logout : ()=>{
			dispatch(logout());
		}
	}

}

class Header extends React.Component{
	constructor(props){
		super(props);
	}
	render(){
		return(
			<div className = "Header">
				{this.props.authenticated ? null : <Redirect to='/'/>}
				<Row style={baseStyle}>	
					<Col >
						<img src={Logo}  className = "imgBorder"/>
						
					</Col>
					
     				
				</Row>
				<Row>
					<div className = "bar">
						<Col style={{color:"white",textAlign:"right",padding:"10px"}}>
							
														 <a style={{ margin:"auto",position:"relative"}} onClick={this.props.logout} > <Icon type="logout"/> Logout</a>

						</Col>
					</div>
				</Row>
				
			</div>
			)
	}

}

export default connect(mapStateToProps, mapDispatchToProps)(Header);

